.Usuario{
	/*background-color: #FFBF00;*/
}

.Usuario td{
	float: left;
}

.Imagen{
	width: 400px;
}

.Imagen img{
	margin: 50px;
	width: 300px;
    height: auto;
    -ms-flex-line-pack: center;
        align-content: center;
    border: 2px solid #f2f2f2;
    border-radius: 50%;
    -webkit-transition-duration: 0.4s;
         -o-transition-duration: 0.4s;
            transition-duration: 0.4s;
}

.Imagen img:hover{
    border-radius: 15%;
}

.Info{
	padding: 30px;
	width: 300px;
}

.Opciones{
	margin: 50px;
	width: 300px;
	text-align: center;
	border-left: 6px solid #FFBF00;
}

.Opciones div{
	padding: 10px;
	-webkit-transition-duration: 0.4s;
	     -o-transition-duration: 0.4s;
	        transition-duration: 0.4s;
}

.Opciones div:hover{
	padding: 10px;
	background-color: black;
	color: #FFBF00;
}


