.Anuncio{
	border-radius: 10px;
}

.Publicacion{
	/*background-color: transparent;
	color: black;
	border:1%;
	border-style:solid;
	border-color: #FFBF00;*/
	border: 1px solid #f2f2f2;
	background: #f2f2f2;
	margin: 10px;
	-webkit-transition-duration: 0.4s;
	     -o-transition-duration: 0.4s;
	        transition-duration: 0.4s;
}

.Publicacion:hover{
	background-color: #FFBF00;
	color: white;
}

a{
    text-decoration: none;
    color: black;
}

.Publicacion td{
	float: left;
}

.Imagen img{
	margin:20px;
	width: 300px;
    height: auto;
    -webkit-transition-duration: 0.4s;
         -o-transition-duration: 0.4s;
            transition-duration: 0.4s;
    background: white;
}
.Imagen img:hover{
	margin:20px;
	width: 300px;
    height: auto;
    border-radius: 15px;
}

.Info{
	padding: 20px;
	width: 400px;
}

.Destacado{
	width: 35px;
    height: auto;
	float: right;
}

.Precio{
	float: right;
	margin-left: 20px;
}

.TituloPublicacion{}

.Descripcion{}

.Opciones{
	width: 140px;
}

.Opcion{
	word-wrap: break-word;
	padding:20px;
	width: 70px;
	text-align: center;
	background: transparent;
	border:2px solid black;
	-webkit-transition-duration: 0.3s;
	     -o-transition-duration: 0.3s;
	        transition-duration: 0.3s;
}

.Opcion:hover{
	background: black;
	border-color: #FFBF00;
	color: #FFBF00;
	font-weight: bolder;
}


