.SelectCategorias select{
	width: 250px;
	height: 30px;
	border-radius: 10px;
	border:1px solid white;
	padding-left: 10px;
	background-color: #FFBF00;
	color:white;
	-webkit-transition-duration: 0.4s;
	     -o-transition-duration: 0.4s;
	        transition-duration: 0.4s;
}

select:focus{
	outline: none;
	border-radius: 10px 10px 0px 0px; 
}



