.NavigationItems {
    margin: 0;
    padding: 0;
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
    height: 100%;
}

@media (min-width: 500px) {
    .NavigationItems {
        -ms-flex-flow: row;
            flex-flow: row;
    }
}

.NavigationItems li {
    padding:17px;
    background-color: transparent;
    color: white;
    border:1%;
    border-style:none none solid none;
    -webkit-transition-duration: 0.4s;
         -o-transition-duration: 0.4s;
            transition-duration: 0.4s;
    border-color: #FFBF00;
}

.NavigationItems li:hover{
    background-color: #FFBF00;
    color: white;
    border-color: #FFBF00;
}

.NavigationItems li:active{
    background-color: #ffffff;
    color: white;
    border-color: transparent;
}

.NavigationItems a{
    text-decoration: none;
}


